/* eslint-disable comma-dangle */
/* eslint-disable quotes */
import CONFIG from "../../globals/config";

const createRestaurantItemTemplate = (restaurant) => `
  <div class="content-section">
  <div class="card">
    <div>
        <img class="resto-item__header__poster lazyload" alt="${
          restaurant.name
        }"
            data-src="${
              restaurant.pictureId
                ? CONFIG.BASE_IMG_URL + restaurant.pictureId
                : "https://picsum.photos/id/666/800/450?grayscale"
            }" crossorigin="anonymous" >
    </div>
    <div class="resto-item__content">
        <h3><a href="#/detail/${restaurant.id}">${restaurant.name}</a></h3>
        <div class="resto-item__rating-location">
            <p class="resto-city">${restaurant.city}</p>
            <p class="resto-rating">Rating : <span>${
              restaurant.rating
            }</span></p>
        </div>
        <p>${restaurant.description}</p>
    </div>
  </div>
</div>
`;

const createRestaurantDetailTemplate = (restaurant) => `
  <div class="detail-information">
    <div class="info-container">
      <img class="restaurant__poster" src="${
        restaurant.pictureId
          ? CONFIG.BASE_IMG_URL + restaurant.pictureId
          : "https://picsum.photos/id/666/800/450?grayscale"
      }" alt="${restaurant.name} "
          alt="${restaurant.name}" crossorigin="anonymous"/>
      <div class="information">
          <div class="restaurant__info">
          <h1 class="resto__title">${restaurant.name}</h1>
              <div class="information-restaurant">
                  <P class="info-restaurant">Kota : ${restaurant.city}</P>
                  <P class="info-restaurant">Alamat : ${restaurant.address}</P>
                  <P class="info-restaurant">Kategori : ${restaurant.categories
                    .map((category) => category.name)
                    .join(" & ")}</P>
                  <P class="info-restaurant">Rating :  ${restaurant.rating}</P>
              </div>
          </div>
          <div class="restaurant__description">
              <h1>Description</h1>
              <p class="desc-item">${restaurant.description}</p>
          </div>

          <div class="food-menu">
              <h1>Food Menu</h1>
              <p class="menu-item">${restaurant.menus.foods
                .map((food) => food.name)
                .join(" | ")}</p>
          </div>

          <div class="drink-menu">
              <h1>Drink Menu</h1>
              <p class="menu-item">${restaurant.menus.drinks
                .map((drink) => drink.name)
                .join(" | ")}</p>
          </div>
      </div>
    </div>
            <div class="review">
                <h1>Review</h1>
                <div class="comment-container">
                  ${restaurant.customerReviews
                    .map(
                      (review) => `
                    <div class="comment-review">
                          <p>${review.name}</p> 
                          <p>(${review.date})</p> 
                          <p>"${review.review}"</p>
                    </div>
                    `
                    )
                    .join("")}
                  </div>
            </div>
    </div>
`;

const createRestoLikeButtonTemplate = () => `
  <button aria-label="like this restaurant" id="likeButton" class="like">
    <i class="fa fa-heart-o" aria-hidden="true"></i>
  </button>
`;

const createRestoUnlikeButtonTemplate = () => `
  <button aria-label="unlike this restaurant" id="likeButton" class="like">
    <i class="fa fa-heart" aria-hidden="true"></i>
  </button>
`;

export {
  createRestaurantItemTemplate,
  createRestaurantDetailTemplate,
  createRestoLikeButtonTemplate,
  createRestoUnlikeButtonTemplate,
};
